.loading-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; /* Full height of the viewport */
    background-color: #010c17; /* Dark blue background */
}

.logo-container {
    position: relative;
    width: 80vw; /* Adjust based on your logo's size in portrait */
    height: 80vw; /* Adjust based on your logo's size in portrait */
    overflow: hidden;
}

@media all and (orientation:landscape) { 
    .logo-container {
        width: 80vh; /* Adjust based on your logo's size in landscape */
        height: 80vh; /* Adjust based on your logo's size in landscape */
    }
}

.logo-top, .logo-bottom {
    position: absolute;
    width: 100%;
    height: 50%;
    background-image: url('../public/Tab_Logo.png'); /* Path to your logo */
    background-size: 100% 200%; /* This ensures the full logo is used */
}

.logo-top {
    top: -50%;
    background-position: top center;
    animation: slideInFromTop 2s ease forwards;
}

.logo-bottom {
    bottom: -50%;
    background-position: bottom center;
    animation: slideInFromBottom 2s ease forwards;
}

@keyframes slideInFromTop {
    0% {
        top: -50%;
    }
    100% {
        top: 0;
    }
}

@keyframes slideInFromBottom {
    0% {
        bottom: -50%;
    }
    100% {
        bottom: 0;
    }
}

/* Apply glow after logo animation */
.logo-container::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-shadow: 0 0 10px 5px rgba(255, 255, 255, 0.6);
    opacity: 0;
    animation: fadeInGlow 2s ease forwards;
}

@keyframes fadeInGlow {
    to {
        opacity: 1;
    }
}









