.glassPanel {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 1rem;
    backdrop-filter: blur(10px);
    padding: 2rem;
    padding-top: 1rem; /* Reduced top padding */
    padding-bottom: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    max-width: 80vw;
   /* max-height: 60vh;*/
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.3);
    position: relative; /* Added for absolute positioning of children */
}

.textSelector {
    position: absolute; /* Positioning relative to glassPanel */
    top: -2rem; /* Adjust this value as needed to move buttons up */
    left: 50%;
    transform: translateX(-50%); /* Center the buttons */
    margin-bottom: 2vh;
    display: flex;
    justify-content: center;
    gap: 1rem;
}

.textSelector button {
    background-color: white;
    color: black;
    border: none;
    padding: 1rem 2rem;
    text-transform: uppercase;
    font-size: 0.8rem;
    font-weight: bold;
    cursor: pointer;
    
    
    -webkit-box-reflect: below 1px linear-gradient(transparent, #0005);
    box-shadow: 0 0 5px #03e9f4, 0 0 15px #03e9f4, 0 0 25px #03e9f4, 0 0 100px #03e914;

    /* Transition for smooth effect */
    transition: background-color 0.3s, color 0.3s, transform 0.3s, box-shadow 0.3s;
    will-change: transform; /* Optimizes animations */
}


.textSelector button:hover, .textSelector button.hovered {
    background-color: black;
    color: white;
    transform: translateY(-5px) scale(1.05);
    box-shadow: 
        0 0 10px #03e9f4, 
        0 0 20px #03e9f4, 
        0 0 30px #03e9f4, 
        0 0 40px #03e914;
}

/* Active button styling, same as hover but without the need for actual hover */
.textSelector button.active {
    background-color: black;
    color: white;
    transform: translateY(-5px) scale(1.05);
    box-shadow: 
        0 0 10px #03e9f4, 
        0 0 20px #03e9f4, 
        0 0 30px #03e9f4, 
        0 0 40px #03e914;

    /* Add a transition-delay only for the transform property */
    transition-delay: 0.1s; /* Adjust this value as needed */

}



