/*   Loan Default    */


.LoanDefaultInputsContainer {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 1.5rem;
    max-width: 100%;
    margin-bottom: 2rem;
    align-items: start; /* aligns items to the start of the container */
    padding: 1rem; /* adds padding inside the container */
    background: rgba(255, 255, 255, 0.15); /* semi-transparent white background */
    backdrop-filter: blur(10px); /* blurring the background */
    border-radius: 10px; /* rounding the corners */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* subtle shadow for depth */
    padding-top: 7%;
}









.LoanDefaultFormElement {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 1rem;
    position: relative;
    color: white;
}

.LoanDefaultFormElement label {
    font-size: 0.9em; /* Using em for relative sizing */
    color: #fff;
    position: absolute;
    left: 10px; /* Align with input padding */
    top: 50%; /* Initially centered vertically */
    transform: translateY(-50%); /* Adjust for exact centering */
    transition: all 0.3s ease;
    pointer-events: none;
}

.LoanDefaultFormElement input {
    width: 100%;
    height: 2.8em; /* Relative height */
    padding: 0 7px;
    border: none;
    outline: none;
    border-radius: 6px; /* Slightly more rounded */
    color: #fff;
    font-size: 1em; /* Relative font size */
    background-color: transparent;
    box-shadow: 3px 3px 10px rgba(0,0,0,1), -1px -1px 6px rgba(255, 255, 255, 0.4);
}

.LoanDefaultFormElement input::placeholder {
    color: transparent;
}

.LoanDefaultFormElement input:focus::placeholder {
    color: #fff; /* Visible placeholder on focus */
}

.LoanDefaultFormElement input:focus {
    outline: none;
    box-shadow: 3px 3px 10px rgba(0,0,0,1),
                -1px -1px 6px rgba(255, 255, 255, 0.4),
                inset 3px 3px 10px rgba(0,0,0,1),
                inset -1px -1px 6px rgba(255, 255, 255, 0.4);
}

/* Moving label up on focus or input not empty, and increasing size */
.LoanDefaultFormElement:focus-within label,
.LoanDefaultFormElement input:valid + label,
.LoanDefaultFormElement input:not(:placeholder-shown) + label {
    top: -17px; /* Adjusted to move above the input */
    font-size: 1em; /* Slightly larger when moved up */
}
















.LoanDefaultFormBoxElement {
    display: flex;
    flex-direction: column;
    width: 100%; /* ensures full width within the grid column */
    margin-bottom: 1rem; /* space between each form element */
}

.LoanDefaultFormBoxElement label {
    font-size: 0.95rem; /* slightly smaller label font size */
    color: #fff; /* white label color for contrast */
    margin-bottom: 0.25rem; /* space between label and input */
}

.LoanDefaultFormBoxElement select {
    padding: 0.5rem; /* padding inside inputs and selects */
    border: 2px solid #fff; /* white border for elements */
    border-radius: 5px; /* rounding corners of inputs and selects */
    background: none; /* transparent background */
    color: #fff; /* text color inside inputs and selects */
    font-size: 1rem; /* larger font size for better readability */
}





