.AWButton {
    --glow-color: rgb(217, 176, 255);
    --glow-spread-color: rgba(191, 123, 255, 0.781);
    --enhanced-glow-color: rgb(231, 206, 255);
    --btn-color: rgb(100, 61, 136);
    border: .25em solid var(--glow-color);
    padding: 1em 3em;
    color: var(--glow-color);
    font-size: 15px;
    font-weight: bold;
    background-color: var(--btn-color);
    border-radius: 1em;
    outline: none;
    box-shadow: 0 0 1em .25em var(--glow-color),
           0 0 4em 1em var(--glow-spread-color),
           inset 0 0 .75em .25em var(--glow-color);
    text-shadow: 0 0 .5em var(--glow-color);
    position: relative;
    transition: all 0.3s;
}

.AWButton::after {
    pointer-events: none;
    content: "";
    position: absolute;
    top: 120%;
    left: 0;
    width: 100%;
    /* Integrate the hover after effect from .btn class */
    background-color: var(--glow-spread-color); /* You might want to adjust this color */
    transition: all 0.3s; /* Ensure smooth transition */

    /* height: 100%;
    filter: blur(2em);
    opacity: .7;
    transform: perspective(1.5em) rotateX(35deg) scale(1, .6); */
    /* Limit the height of the pseudo-element to reduce the vertical spread */
    height: 1em; /* Smaller height to reduce the spread */
    opacity: 0.5; /* Reduced opacity for a subtler effect */
    filter: blur(1em); /* Reduced blur for a tighter glow effect */
    transform: perspective(1em) rotateX(25deg) scale(1, 1); /* Adjust the scale for a flatter effect */
}

.AWButton:hover {
    color: var(--btn-color);
    background-color: var(--glow-color);
    /* Adapt hover effect from .btn class */
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(27, 27, 27, .5), /* Add this shadow */
                0 0 1em .25em var(--glow-color), /* Keep existing glow */
                0 0 4em 2em var(--glow-spread-color); /* Keep existing glow spread */
  
}

.AWButton:active {
    /* Adapt active effect from .btn class */
    transform: translateY(-3px);
    box-shadow: 0 0 0.6em .25em var(--glow-color), /* Adapted active shadow */
                0 0 2.5em 2em var(--glow-spread-color); /* Keep glow spread */
}

.AWButton:hover::after {
    /* Adapt the hover after effect from .btn class */
    transform: scaleX(1.4) scaleY(1.5);
    opacity: 0; /* Hide the pseudo-element on hover */
}




.cardContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 2em; /* Space above the card container */
}

.card {
    position: relative;
    width: 210px;
    height: 254px;
    border-radius: 4px;
    margin: 0.5em;
    overflow: hidden; /* Ensures that the border-radius clips the children elements */
    transition: transform 0.3s ease, box-shadow 0.3s ease; /* Smooth transition for transform and shadow */
    will-change: transform; /* Optimize for animations */
}

.card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease; /* Smooth transition for the image scaling */
}

.card-text {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.5s ease;
    background-color: rgba(0, 0, 0, 0); /* Transparent background */
    color: #fff; /* Change text color */
}

.card-text p {
    transition: transform 0.5s ease, color 0.3s ease; /* Smooth transition for text color and transform */
    text-align: center;
    font-size: 1.2em; /* Larger font size */
    font-weight: bold; /* Bold font weight */
    letter-spacing: 0.1em;
    margin: 0;
    color: transparent; /* Text is invisible initially */
    text-shadow: 0px 0px 8px rgba(255, 255, 255, 0.5); /* Text shadow for better readability */
}

.card:hover {
    transform: scale(1.05); /* Scale the card up slightly */
    box-shadow: 0 5px 15px rgba(0,0,0,0.3); /* Add shadow for depth */
}

.card:hover img {
    transform: scale(1.1); /* Scale the image up slightly */
}

.card:hover .card-text {
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
}

.card:hover .card-text p {
    color: #fff; /* Text becomes visible */
}










